import React, { Component } from 'react';
import './about.css';
import { Grid, Typography } from '@material-ui/core';
import { UsersModel } from '../../models/users-model';
import { Redirect } from 'react-router-dom';

interface AboutState{
    userInfo:UsersModel
    enableText:boolean
}

export class About extends Component<any,AboutState>{
    constructor(props:any){
        super(props)
        this.state={
            enableText:false,
            userInfo:new UsersModel()
        }
    }

    async componentDidMount(){
        setTimeout(() => {
            this.setState({enableText:true})
        }, 2000);
        const userInfo = await this.props.isUserLogged();
        this.setState({userInfo})
    }
    render(){
        return(
            this.state.enableText ? this.state.userInfo === false ? <Redirect to='/'/> :
            <Grid container alignItems="center" justify="center" direction="column">
                <Typography variant='h2' component='h2'>Welcome To SkySurf</Typography>
            </Grid> : <div className='loadingGif'></div>
        )
    }
}