import React, { Component, ChangeEvent } from 'react';
import './admin-space.css'
import { VacationModel } from '../../models/vacation-model';
import { UsersModel } from '../../models/users-model';
import { Grid, Typography, Paper, Box, TextField, Button, Input, Modal,} from '@material-ui/core';
import axios from 'axios';
import {Config} from '../../config'
import ReactCardFlip from 'react-card-flip';
import { Redirect } from 'react-router-dom';
import Swal from 'sweetalert2';
import socketIOClient  from "socket.io-client";
const socket = socketIOClient(Config.serverUrl);






interface AdminSpaceState{
    modifiedVacation:VacationModel
    vacations:VacationModel[]
    enableText:boolean
    adminInfo:UsersModel
    isFlipped:boolean
    flippedVacation:number
    modelOpen:boolean
    errors:{startDateError:string,endDateError:string,descriptionError:string,destinationError:string,priceError:string}

}

export class AdminSpace extends Component<any,AdminSpaceState>{
    constructor(props:any){
        super(props)
        this.state={
            adminInfo: new UsersModel(),
            modifiedVacation: new VacationModel(),
            vacations:[],
            enableText:false,
            isFlipped:false,
            flippedVacation:null,
            modelOpen:false,
            errors:{startDateError:'*',endDateError:'*',descriptionError:'*',destinationError:'*',priceError:'*'}
        }
    }

    private handleClick=(vacationId:number)=>{
        this.setState({flippedVacation:vacationId})
        this.setState(prevState => ({ isFlipped: !prevState.isFlipped }));
        let modifiedVacation = {...this.state.modifiedVacation};
        modifiedVacation.description = undefined
        modifiedVacation.destination = undefined
        modifiedVacation.startDate = undefined
        modifiedVacation.endDate = undefined
        modifiedVacation.price = undefined
        modifiedVacation.vacationId = vacationId;
        this.setState({modifiedVacation});
    }

    async componentDidMount(){
        setTimeout(() => {
            this.setState({enableText:true})
        }, 2000);
       await this.getVacations();
       const adminInfo = await this.props.isAdminLogged();
       this.setState({adminInfo})
        socket.on("admin-change", (UpdatedVacations:any) => {
            const vacations = UpdatedVacations;
            this.setState({vacations})
        });
    }

public getVacations=async()=>{
    try{
       const url = Config.serverUrl;
       const response = await axios.get<VacationModel[]>(`${url}/api/vacations/getAllVacations`);
       const vacations = response.data;
       this.setState({vacations});
    }catch(err){
        console.log(err.message)
    }
}
    public setDestination=(args:ChangeEvent<HTMLInputElement>)=>{
        const destination = args.target.value;
        const modifiedVacation = {...this.state.modifiedVacation};
        modifiedVacation.destination = destination;
        this.setState({modifiedVacation});
        this.validateForm();
    }
    public setDescription=(args:ChangeEvent<HTMLInputElement>)=>{
        const description = args.target.value;
        const modifiedVacation = {...this.state.modifiedVacation};
        modifiedVacation.description = description;
        this.setState({modifiedVacation});
        this.validateForm();
    }
    public setStartDate=(args:ChangeEvent<HTMLInputElement>)=>{
        const startDate = args.target.value;
        const modifiedVacation = {...this.state.modifiedVacation};
        modifiedVacation.startDate = startDate;
        this.setState({modifiedVacation});
        this.validateForm();
    }
    public setEndDate=(args:ChangeEvent<HTMLInputElement>)=>{
        const endDate = args.target.value;
        const modifiedVacation = {...this.state.modifiedVacation};
        modifiedVacation.endDate = endDate;
        this.setState({modifiedVacation});
        this.validateForm();
    }
    public setPrice=(args:ChangeEvent<HTMLInputElement>)=>{
        const price = +args.target.value;
        const modifiedVacation = {...this.state.modifiedVacation};
        modifiedVacation.price = price;
        this.setState({modifiedVacation});
        this.validateForm();
    }



    private modifyVacation =async(vacationId:number) =>{
        try{
        const url = Config.serverUrl;
        await axios.post<VacationModel>(`${url}/api/vacations/modify`,this.state.modifiedVacation);
        this.handleClick(vacationId);
        }catch(err){
            console.log(err.message)
        }
    }

    private deleteVacation=async(vacationId:number)=>{
        try{
            const url = Config.serverUrl;
            await axios.delete(`${url}/api/vacations/removeVacation/${vacationId}`)
            this.handleClick(vacationId);
        }catch(err){
            console.log(err.message)
        }
    }

    public openModel=()=>{
        this.setState({modelOpen:true})
    }
    
    public closeModel=()=>{
        this.setState({modelOpen:false})
        let modifiedVacation = {...this.state.modifiedVacation};
        modifiedVacation.description = undefined
        modifiedVacation.destination = undefined
        modifiedVacation.startDate = undefined
        modifiedVacation.endDate = undefined
        modifiedVacation.price = undefined
        this.setState({modifiedVacation})
    }


    public validateForm=()=>{
        let errors = {...this.state.errors}
        errors.destinationError = ''
        errors.descriptionError = ''
        errors.startDateError = ''
        errors.endDateError = ''
        errors.priceError = ''
        if(this.state.modifiedVacation.destination === undefined || this.state.modifiedVacation.destination === ''){
            errors.destinationError = '*'
        }
        if(this.state.modifiedVacation.description === undefined || this.state.modifiedVacation.description === ''){
            errors.descriptionError = '*'
        }
        if(this.state.modifiedVacation.startDate === undefined || this.state.modifiedVacation.startDate === ''){
            errors.startDateError = '*'
        }
        if(this.state.modifiedVacation.startDate > this.state.modifiedVacation.endDate){
            errors.startDateError = '*'
        }
        if(this.state.modifiedVacation.endDate === undefined || this.state.modifiedVacation.endDate === ''){
            errors.endDateError = '*'
        }
        if(this.state.modifiedVacation.price === undefined || this.state.modifiedVacation.price === null){
            errors.priceError = '*'
        }
        this.setState({errors})
        if(
            this.state.errors.descriptionError === ''
            && this.state.errors.destinationError === ''
            && this.state.errors.startDateError === ''
            && this.state.errors.endDateError === ''
            && this.state.errors.priceError === ''){
                return true
        }
        return false
    }

    public handleSubmit=async(event:any)=>{
        const isValid = this.validateForm();
        if(!isValid){
            event.preventDefault();
        }
    }
    

    render(){
        
        return(
            
            this.state.enableText  ? this.state.adminInfo === false || this.state.adminInfo.isAdmin !== 1 ? <Redirect to='/'/> :
            <Grid container direction="column"  alignItems="center" justify="center" style={{ minHeight: '85vh' }}>
                <Box m={4}>
                    <Typography component="h1" variant="h2">
                        Admin Zone
                    </Typography>
                </Box>
                <Box m={2}>
                    <Typography component="h1" variant="h5">
                        Press On a vacation to modify or delete it, be careful users will be notified immediately! 
                    </Typography>
                </Box>
                <Grid container direction="row"  alignItems="center" justify="center">
                    {this.state.vacations.map(v=>{
                       return <Grid item container lg={4} xs={8} md={5} justify="center" className='vacationContainer' key={v.vacationId}>
                                    <ReactCardFlip  isFlipped={this.state.isFlipped && this.state.flippedVacation === v.vacationId} flipDirection="horizontal">
                                        <Paper  className='vacationContainerBackground' onClick={()=>{this.handleClick(v.vacationId)}}
                                        style={{minWidth:'400px',minHeight:'500px' ,background:` linear-gradient(0deg, rgba(216, 95, 166, 0.541), rgba(255, 189, 103, 0.438)), url("${Config.serverUrl}/uploads/${v.vacationImg}")`}}>
                                            <Box ml={2}>
                                                <Grid container direction="row" justify="flex-start" alignItems="flex-start">
                                                    <Typography  gutterBottom variant="subtitle1">{v.destination}</Typography>
                                                </Grid>
                                            </Box>
                                            <Box mr={2}>
                                            <Grid container direction="row" justify="flex-end" alignItems="flex-end">
                                            </Grid>
                                            </Box>
                                            <Grid container direction="row" justify="center" alignItems="center" style={{height:'170px'}}>
                                                <Typography gutterBottom variant="subtitle1">{v.description}</Typography>
                                            </Grid>
                                            <Grid container direction="row" justify="center" alignItems="center">
                                                <Typography gutterBottom variant="subtitle1">Dates - {v.startDate} - {v.endDate}</Typography>
                                            </Grid>
                                            <Grid container direction="row" justify="center" alignItems="flex-end" >
                                                <Typography gutterBottom variant="subtitle1">Now for Just - {v.price} $ per Person!</Typography>
                                            </Grid>
                                        </Paper>
                                        <Paper className='vacationContainerBackground' 
                                        style={{minWidth:'400px',minHeight:'500px', background:` linear-gradient(0deg, rgba(216, 95, 166, 0.541), rgba(255, 189, 103, 0.438)), url("${Config.serverUrl}/uploads/${v.vacationImg}")`}}>
                                            <Box ml={2}>
                                                <Grid container direction="row" justify="center" alignItems="center">
                                                    <TextField onChange={this.setDestination}
                                                       value={this.state.modifiedVacation.destination || ''}
                                                       name='destination' /> 
                                                    <Typography gutterBottom variant="subtitle1">Destination</Typography>
                                                </Grid>
                                            </Box>
                                            <Grid container direction="row" justify="center" alignItems="center" style={{height:'170px'}}>
                                                <TextField onChange={this.setDescription}
                                                value={this.state.modifiedVacation.description || ''}
                                                name='description'/>
                                                <Typography gutterBottom variant="subtitle1">Description</Typography>
                                            </Grid>
                                            <Grid container direction="row" justify="center" alignItems="center">
                                                <TextField onChange={this.setStartDate}
                                                value={this.state.modifiedVacation.startDate || ''}                                              
                                                id="startDate"
                                                label="Start Date"
                                                type="date"
                                                InputLabelProps={{shrink: true,}}/>
                                                <Typography gutterBottom variant="subtitle1">Start Date</Typography>
                                            </Grid>
                                            <Grid container direction="row" justify="center" alignItems="center">
                                                <TextField onChange={this.setEndDate}
                                                value={this.state.modifiedVacation.endDate || ''}                                              
                                                id="endDate"
                                                label="End Date"
                                                type="date"
                                                InputLabelProps={{shrink: true,}}/>
                                                <Typography gutterBottom variant="subtitle1">End Date</Typography>
                                            </Grid>
                                            <Grid container direction="row" justify="center" alignItems="flex-end" >
                                                <TextField onChange={this.setPrice}
                                                value={this.state.modifiedVacation.price || ''}  
                                                name='price'/>
                                                <Typography gutterBottom variant="subtitle1">Price</Typography>
                                            </Grid>
                                            <Grid container direction="row" justify="center" alignItems="flex-end">
                                            <iframe name={`${v.vacationId}`} title={`${v.vacationId}`} style={{display:'none'}}></iframe>
                                                <form
                                                   target={`${v.vacationId}`}
                                                    method="post"
                                                    action={`${Config.serverUrl}/api/vacations/modify`}
                                                    encType="multipart/form-data">
                                                    <input defaultValue={`${v.vacationId}`} name='vacationId' style={{display:'none'}} />
                                                    <input type="file" name="vacationImg"  accept="image/*"/>
                                                    <br/>
                                                    <Grid container direction="row" justify="center" alignItems="flex-end">
                                                        <Button type='submit' variant="contained" color="primary" onClick={()=>{this.modifyVacation(v.vacationId)}}>
                                                                    Submit Changes
                                                        </Button>
                                                    </Grid>
                                                </form>        
                                            </Grid>
                                            <Box mt={2}>
                                                <Grid  container direction="row" justify="center" alignItems="flex-end">
                                                    <Button variant="contained" color='secondary' onClick={()=>{this.deleteVacation(v.vacationId)}}>Delete Vacation</Button>
                                                </Grid>
                                            </Box>
                                        </Paper>
                                    </ReactCardFlip>
                                </Grid>   
                    })}
                </Grid>
                <Grid container direction="column" justify="center" alignItems="center">
                    <Typography component="h1" variant="h3">
                        Add Vacation 
                    </Typography>
                    <Box mt={2}>
                        <Typography component="h1" variant="h5">
                            Press The Button and use the pop-up Model to add a new vacation to the site.
                        </Typography>
                    </Box>
                    <Box m={5}>
                        <Button onClick={()=>{this.openModel()}} variant="contained" color='primary'>Press To Add Vacation</Button>
                    </Box>
                    <Modal
                        open={this.state.modelOpen}
                        onClose={this.closeModel}
                        aria-labelledby="simple-modal-title"
                        aria-describedby="simple-modal-description">
                            <Box mt={10} style={{marginLeft:'50%',marginRight:"50%"}}>
                                <Grid container direction="row" justify="center" alignItems="center" >
                                <Paper className='modelWindow'>
                                    <Grid container direction="column" justify="center" alignItems="center">
                                        <Grid container direction="row" justify="center" alignItems="center">
                                                <Typography component='h1' variant='h5'>Add Vacation</Typography>
                                        </Grid>
                                    <Box ml={2}>
                                                <Grid container direction="row" justify="center" alignItems="center">
                                                    <TextField onChange={this.setDestination}
                                                        label="Destination"
                                                       value={this.state.modifiedVacation.destination || ''}
                                                       name='destination'
                                                       error={this.state.modifiedVacation.destination === '' || this.state.modifiedVacation.destination === undefined}
                                                       helperText={this.state.modifiedVacation.destination === '' || this.state.modifiedVacation.destination === undefined ? 'Empty Field' : ''} /> 
                                                </Grid>
                                            </Box>
                                            <Grid container direction="row" justify="center" alignItems="center" style={{height:'170px'}}>
                                                <TextField onChange={this.setDescription}
                                                label="Description"
                                                value={this.state.modifiedVacation.description || ''}
                                                name='description'
                                                error={this.state.modifiedVacation.description === '' || this.state.modifiedVacation.description === undefined}
                                                helperText={this.state.modifiedVacation.description === '' || this.state.modifiedVacation.description === undefined ? 'Empty Field' : ''}/>
                                            </Grid>
                                            <Grid container direction="row" justify="center" alignItems="center">
                                                <TextField onChange={this.setStartDate}
                                                value={this.state.modifiedVacation.startDate || ''}                                              
                                                id="startDate"
                                                label="Start Date"
                                                type="date"
                                                InputLabelProps={{shrink: true,}}
                                                error={this.state.modifiedVacation.startDate === '' || this.state.modifiedVacation.startDate === undefined 
                                                || this.state.modifiedVacation.startDate > this.state.modifiedVacation.endDate}
                                                helperText={this.state.modifiedVacation.startDate === '' || this.state.modifiedVacation.startDate === undefined ? 'Empty Field' : '' 
                                                || this.state.modifiedVacation.startDate > this.state.modifiedVacation.endDate ? 'Starting date cannot be bigger then ending date' : ''}/>
                                            </Grid>
                                            <Grid container direction="row" justify="center" alignItems="center">
                                                <TextField onChange={this.setEndDate}
                                                value={this.state.modifiedVacation.endDate || ''}                                              
                                                id="endDate"
                                                label="End Date"
                                                type="date"
                                                InputLabelProps={{shrink: true,}}
                                                error={this.state.modifiedVacation.endDate === '' || this.state.modifiedVacation.endDate === undefined 
                                                || this.state.modifiedVacation.startDate > this.state.modifiedVacation.endDate}
                                                helperText={this.state.modifiedVacation.endDate === '' || this.state.modifiedVacation.endDate === undefined ? 'Empty Field' : '' 
                                                || this.state.modifiedVacation.startDate > this.state.modifiedVacation.endDate ? 'Starting date cannot be bigger then ending date' : ''}/>
                                            </Grid>
                                            <Grid container direction="row" justify="center" alignItems="flex-end" >
                                                <TextField onChange={this.setPrice}
                                                label="Price"
                                                value={this.state.modifiedVacation.price || ''}  
                                                name='price'
                                                error={this.state.modifiedVacation.price === null || this.state.modifiedVacation.price === undefined}
                                                helperText={this.state.modifiedVacation.price === null || this.state.modifiedVacation.price === undefined ? 'Empty Field' : ''} />
                                            </Grid>
                                            <Grid container direction="row" justify="center" alignItems="flex-end">
                                            <iframe name='formRedirect' title='formRedirect' style={{display:'none'}}></iframe>
                                                <form    
                                                   target= 'formRedirect'
                                                    method="post"
                                                    action={`${Config.serverUrl}/api/vacations/addVacation`}
                                                    encType="multipart/form-data">
                                                    <input name='vacationId' style={{display:'none'}} />    
                                                    <Box mt={2}>
                                                        <Button variant="contained" color="secondary"><input type="file" name="vacationImg"  accept="image/*"/>Upload Image</Button>
                                                    </Box>                                        
                                                    <input type='txt' name='description' defaultValue={this.state.modifiedVacation.description || ''} style={{display:'none'}}/>
                                                    <input type='txt' name='destination' defaultValue={this.state.modifiedVacation.destination || ''} style={{display:'none'}}/>
                                                    <input type='txt' name='startDate' defaultValue={this.state.modifiedVacation.startDate || ''} style={{display:'none'}}/>
                                                    <input type='txt' name='endDate' defaultValue={this.state.modifiedVacation.endDate || ''} style={{display:'none'}}/>
                                                    <input type='txt' name='price' defaultValue={this.state.modifiedVacation.price || ''} style={{display:'none'}}/>
                                                    <br/>
                                                    <Grid container direction="row" justify="center" alignItems="flex-end">
                                                        <Button type='submit' variant="contained" color="primary" onClick={this.handleSubmit}>
                                                                    Submit Changes
                                                        </Button>
                                                    </Grid>
                                                </form>        
                                            </Grid>
                                    </Grid>
                                </Paper>
                            </Grid>
                        </Box>
                    </Modal>
                </Grid>
            </Grid>
        : <div className='loadingGif'></div>)
    }
}
