import React, { Component } from 'react';
import './admin-chart.css';
import { Grid, Paper, Typography, Box } from '@material-ui/core';
import { UsersModel } from '../../models/users-model';
import { Redirect } from 'react-router-dom';
import { Animation,HoverState,EventTracker  } from '@devexpress/dx-react-chart';
import {
    Chart,
    BarSeries,
    Title,
    ArgumentAxis,
    ValueAxis,
  } from '@devexpress/dx-react-chart-material-ui';
import { VacationModel } from '../../models/vacation-model';
import axios from 'axios';
import {Config} from '../../config'




interface AdminChartState{
    adminInfo:UsersModel
    enableText:boolean
    chartData:VacationModel[]
}


export class AdminChart extends Component<any,AdminChartState>{
    constructor(props:any){
        super(props)
        this.state={
            adminInfo:new UsersModel(),
            enableText:false,
            chartData:[]
        }
    }

   async componentDidMount(){
       setTimeout(() => {
           this.setState({enableText:true})
       }, 2000);
        const adminInfo = await this.props.isAdminLogged();
        this.setState({adminInfo})
        await this.getVacations();
        console.log(this.state.chartData)

    }

    public getVacations=async()=>{
        try{
           const url = Config.serverUrl;
           const response = await axios.get<VacationModel[]>(`${url}/api/vacations/getAllVacations`);
           const chartData = response.data;
           this.setState({chartData});
        }catch(err){
            console.log(err.message)
        }
    }
    render(){
        return(
        this.state.enableText ? this.state.adminInfo === false || this.state.adminInfo.isAdmin !== 1 ? <Redirect to='/'/> : 
            <Grid container alignItems="center" justify="center" direction="column"> 
                <Box mb={5}>
                    <Typography>
                        The Chart below shows the numbers of followers per Vacation (Only vacations with more then one follower will show here)
                    </Typography>
                </Box>
                <Grid className='chart' >
                    <Chart  
                    data={this.state.chartData.filter(v=> v.numberOfFollowers > 0)}
                    >
                    <ArgumentAxis/>
                    <ValueAxis  position="left"  />
                    
                    <BarSeries

                        barWidth={0.7}
                        valueField="numberOfFollowers"
                        argumentField="destination"
                        color={"#ffaa66"}
                    />
                    <Title text="Vacation Following" />
                    <Animation />
                    <EventTracker />
                    <HoverState />

                    </Chart>
                    </Grid>
            </Grid>
        :<div className='loadingGif'></div>)
    }
}