import React, { Component } from 'react';
import './layout.css'
import {BrowserRouter, Switch, Route, Redirect} from 'react-router-dom';
import { Home } from '../home/home';
import {Login} from '../login/login'
import { Register } from '../register/register';
import { Menu } from '../menu/menu';
import { AdminSpace } from '../admin-space/admin-space';
import { PageNotFound } from '../page-not-found/page-not-found';
import { Typography, Grid } from '@material-ui/core';
import { UsersModel } from '../../models/users-model';
import axios from 'axios';
import {Config} from '../../config'
import { AdminChart } from '../admin-chart/admin-chart';
import { About } from '../about/about';



axios.defaults.withCredentials = true;



interface LayOutState{
    showMenu:boolean
}


export class Layout extends Component<any,LayOutState>{
    constructor(props:any){
        super(props)
        this.state={
            showMenu:true
        }
    }
    private userLogged = async()=>{
        try{
            const url = Config.serverUrl;
            const response = await axios.get<UsersModel>(`${url}/api/userLogged`);
            return response.data;
        }catch(err){
            if(err.response.status === 401){
                return false
            }
        }
    }

    private adminLogged = async()=>{
        try{
            const url = Config.serverUrl;
            const response = await axios.get<UsersModel>(`${url}/api/userLogged`);
            return response.data;
        }catch(err){
            if(err.response.status === 401){
                return false
            }
        }
    }
    public disableMenu=()=>{
        this.setState({showMenu:false})
    }

    public enableMenu=()=>{
        this.setState({showMenu:true})
    }



    render(){
        return(
            <Grid container
            direction="column"
            alignItems="center"
            style={{ minHeight: '100vh'}}>
                <BrowserRouter>
                {this.state.showMenu && 
                            <Grid container alignItems="flex-start"  direction="column">
                            <aside>
                                <Menu isUserLogged ={this.userLogged}/>
                            </aside>
                        </Grid>}
                    <header className='header'>
                        <Grid container alignItems="center"  direction="column"  style={{ minHeight: '20vh' }}>
                            <Typography component="h1" variant="h2">
                                SkySurf
                            </Typography>
                            <Grid container item lg={12}  justify="center" alignItems="center">
                                <Typography component="h1" variant="h6" >
                                    - Your Indulgent vacation is only -
                                </Typography>
                            </Grid>
                            <Grid container item lg={12} justify="center" alignItems="center">
                                <Typography component="h1" variant="h6" >
                                    - A Few Clicks Away -
                                </Typography>
                            </Grid>
                        </Grid>
                    </header>
                    <main>
                        <Switch>
                            <Route path='/home' render={props=><Home {...props} isUserLogged ={this.userLogged} enableMenu={this.enableMenu}/>}exact/>
                            <Route path='/about' render={props=><About {...props} isUserLogged ={this.userLogged} enableMenu={this.enableMenu}/>}exact/>
                            <Route path='/register' render={props=><Register {...props} enableMenu={this.disableMenu}/>}/>
                            <Route path='/login' render={props=><Login {...props} enableMenu={this.disableMenu}/>}/>
                            <Route path='/adminSpace' render={props=><AdminSpace {...props} isAdminLogged={this.adminLogged}/>}/>
                            <Route path='/adminChart' render={props=><AdminChart {...props} isAdminLogged={this.adminLogged}/>}/>
                            <Redirect from='/' to='/register' exact/>
                            <Route component={PageNotFound} /> 
                        </Switch>
                    </main>
                    <footer>

                    </footer>
                </BrowserRouter>
            </Grid>
        )
    }
}